<template>
    <div class="m-paid_attachment">
        <input class="u-data-input" type="file" id="face_file" @change="processFile" accept=".jx3dat, .dat, .ini" />
        <el-button type="primary" @click="selectData" icon="el-icon-upload2">{{ `${name}` }}</el-button>
        <span class="u-data-ready" v-show="data.uuid">
            <i class="el-icon-success"></i>
            
        </span>
    </div>
</template>

<script>
import { uploadFaceFile } from "@/service/face.js";
import { uploadBodyFile } from "@/service/body.js";
import { load } from "@jx3box/jx3box-facedat/src/DataRouter.js";
export default {
    name: "paid_attachment",
    props: {
        body: {
            type: Number,
            default: 2,
        },
        describe: {
            type: String,
            default: "",
        },
        type: {
            type: String,
            default: "face",
        },
    },
    data() {
        return {
            data: {
                id: "",
                uuid: "",
                object: "",
                json: "",
                name: "",
            },
        };
    },
    computed: {
        name() {
            const data = {
                face: "",
                body: "",
            };
            return data[this.type];
        },
    },
    methods: {
        selectData() {
            let fileInput = document.getElementById("face_file");
            fileInput.dispatchEvent(new MouseEvent("click"));
        },
        uploadFile(formdata) {
            const data = {
                face: uploadFaceFile,
                body: uploadBodyFile,
            };
            return data[this.type](formdata);
        },
        uploadData(file) {
            // 
            let formdata = new FormData();
            formdata.append("jx3dat", file);
            formdata.append("body", this.body);
            formdata.append("describe", this.describe);
            formdata.append("data", this.data.json);
            this.uploadFile(formdata).then((res) => {
                this.data.uuid = res.data.data.uuid;
                this.data.id = res.data.data.id;
                this.data.name = res.data.data.name;
                this.$message({
                    message: "",
                    type: "success",
                });
                this.$emit("update:data", this.data);
            });
        },
        processFile(e) {
            let file = e.target.files[0];
            // 64kb
            if (file && file.size > 65536) {
                this.$message({
                    message: " 64KB ",
                    type: "error",
                });
                return;
            }
            // 

            this.parseAndUpload(file);
            e.target.value = "";
        },
        parseAndUpload(file) {
            if (!FileReader) return;
            if (!file) return;
            const vm = this;

            //  File  ArrayBuffer 
            let fr = new FileReader();
            fr.onload = function (e) {
                console.log("......");
                let json = "";
                let object = "";
                try {
                    const result = load(e.target.result);
                    if (!result)
                        return vm.$notify.error({
                            title: "",
                            message: "",
                        });
                    if (vm.type === "body" && result.type.startsWith("face")) {
                        return vm.$notify.error({
                            title: "",
                            message: "",
                        });
                    }
                    if (vm.type === "face" && result.type === "body") {
                        return vm.$notify.error({
                            title: "",
                            message: "",
                        });
                    }
                    object = result.data;
                    console.log(object);
                    if (object) {
                        json = JSON.stringify(object);
                    }
                } catch (ex) {
                    console.log(ex);
                    vm.$notify.error({
                        title: "",
                        message: "",
                    });
                    return;
                }

                // 
                if (object && json) {
                    setTimeout(
                        () =>
                            vm.$notify({
                                title: "",
                                message: "",
                                type: "success",
                            }),
                        0
                    );
                    vm.data.json = json;
                    vm.uploadData(file);
                    vm.data.object = object;
                }
            };
            fr.onerror = function (e) {
                vm.$notify.error({
                    title: "",
                    message: "",
                });
            };
            fr.readAsArrayBuffer(file);
        },
        clearData() {
            this.data = {
                id: "",
                uuid: "",
                object: null,
                json: "",
                name: "",
            };
            this.$emit("update:data", this.data);
        },
    },
};
</script>

<style lang="less" scoped>
.m-paid_attachment {
    .u-data-input {
        display: none;
    }

    .u-data-ready {
        margin-left: 10px;
        i {
            color: #67c23a;
        }
    }
}
</style>
